<template>
	<div class="search" @mouseover="showInput" :class="{'active': isActiveSearch}">
		<div class="search-wapper">
			<div class="circle"><div class="line"></div></div>
			<input type="text" class="search-input" id="searchInput">
			<span class="icon" :style="`background-image: url('${searchType.google.icon}')`"></span>
			<span class="button"></span>
		</div>
	</div>
</template>

<style lang="less" scoped>
@import '../assets/less/main.less';

@searchWidth: 500px; @circleSize: 45px; @borderWidth: 3px; @color: #ccc; @inputColor: #666;
@keyframes changeToInput {
		// 0% {

		// }
		100% {
			max-width: @searchWidth; border-width: 2px; width: 100%;
		}
}
@keyframes changePos {
		// 0% {
		// 	transform: rotate(45deg) translate3d(0,0,0);
		// }
		50% {
			transform: rotate(45deg) translate3d(20px,10px,0);
		}
		100% {
			transform: rotate(90deg) translate3d(20px,-26px,0) scaleY(0.5); height: 1px; width: 22px; background-color: @inputColor;
		}
}

.fadeIn {
	transition: opacity 0.3s linear 0.8s;
}

.search {
	& { display: flex; justify-content: center; }
	.search-wapper { position: relative; width: 100%; max-width: @searchWidth; }
	.circle, .search-input { width: @circleSize; height: @circleSize; border-radius: @circleSize; border:@borderWidth solid @color;  transform-origin: center; }
	.line { width: @circleSize - 15; height: @borderWidth; background-color: @color; border-radius: @borderWidth; transform: rotate(45deg) translate3d(53px, 9px, 0); transform-origin: center; transition: opacity 0.2s linear 0.7s; }
	.search-input { width: 100%; max-width: @searchWidth; font-size: 18px; color: @inputColor; background-color: transparent; padding: 0 36px; box-sizing: border-box; position: absolute; left: 50%; top: 0; transform: translateX(-50%); opacity: 0; height: 0; border-width: 2px; .fadeIn; .ellipsis; }
	.icon { width: 20px; height: 20px; background-size: contain; border-radius: 50%; position: absolute; top: 12px; left: 12px; box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1); opacity: 0; .fadeIn; }
	.button {
		& { position: absolute; right: 20px; top: 9px; opacity: 0; .fadeIn; cursor: pointer; display: block; width: 30px; height: 30px; }
		&:before, &:after{
			content: ''; width: 15px; height: 2px; background-color: @color; display: inline-block; transform-origin: center; position: relative;
		}
		&:before { transform: rotate(-45deg); left: 15px; top: 10px; }
		&:after { transform: rotate(45deg);  }
	}
	&.active {
		.circle { animation: changeToInput 0.5s ease-in-out .3s; animation-fill-mode: forwards; }
		.line { animation: changePos 0.5s ease-in-out; animation-fill-mode: forwards; opacity: 0; }
		.search-input { opacity: 1; height: @circleSize; }
		.icon { opacity: 1; }
		.button { opacity: 1; }
	}
}
</style>

<script>
export default {
	name: 'Search',
	data(){
		return {
			isActiveSearch: false,
			searchType: {
				google: {
					icon: 'https://www.baidu.com/favicon.ico'
				}
			}
		}
	},
	// mounted() {
	// },
	methods: {
		showInput() {
			this.isActiveSearch = true
			window.document.getElementById('searchInput').focus()
		}
	}
}
</script>



