<template>
	<div>
		<top-bar v-if="showTopBar"></top-bar>
		<div class="index">
			<search class="search"></search>
			<sort-list class="sort-list"></sort-list>
			<div class="read-title">稍后阅读</div>
			<read-list class="read-list"></read-list>
		</div>
	</div>
</template>

<script>
import SortList from './Sort-list'
import Search from './Search'
import TopBar from './Top-bar'
import ReadList from './Read-list'

export default {
	name: 'Index',
	data () {
		return {
			showTopBar: false
		}
	},
	components: {
		SortList,
		Search,
		TopBar,
		ReadList
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
	.index { max-width: 1400px; margin: 0 auto; }
	.search { margin-top: 40px; }
	.sort-list { margin-top: 40px; }
	.read-title { text-align: left; color: #888; padding-left: 1%; margin-top: 30px; font-size: 16px; }
	.read-list { margin-top: 30px; }
</style>



