<template>
	<div>
		<draggable :options="sortListOptions" @start="drag=true" @end="drag=false" class="item-con">
			<div v-for="item in list" class="item">
				<p class="title" :class="{'no-sub': !item.subName}" :title="item.name">{{item.name}}</p>
				<p v-show="item.subName" class="sub-title" :title="item.subName">{{item.subName}}</p>
				<span class="icon" :style="`background-image: url('${item.icon}')`"></span>
			</div>
			<div class="empty-item" v-for="item in empty"></div>
		</draggable>
	</div>
</template>

<script>
import draggable from "vuedraggable"

export default {
	components: {
		draggable
	},
	data() {
		return {
			sortListOptions: {
				animation: 200
			},
			empty: Array(5),
			list: [
				{
					name: '百度',
					subName: '百度一下你就知道',
					icon: 'https://www.baidu.com/favicon.ico'
				},
				{
					name: '谷歌',
					icon: 'https://www.google.com/favicon.ico'
				},
				{
					name: '谷歌',
					icon: 'https://www.google.com/favicon.ico'
				},
				{
					name: '百度',
					subName: '百度一下你就知道',
					icon: 'https://www.baidu.com/favicon.ico'
				},
				{
					name: '谷歌',
					icon: 'https://www.google.com/favicon.ico'
				},
				{
					name: '谷歌',
					icon: 'https://www.google.com/favicon.ico'
				},
				{
					name: '谷歌',
					icon: 'https://www.google.com/favicon.ico'
				},
				{
					name: '谷歌',
					icon: 'https://www.google.com/favicon.ico'
				}
			]
		}
	}
}
</script>

<style lang="less" scoped>
	.item-con {
		display: flex; justify-content: space-between; flex-wrap: wrap;
	}
	.item {
		@itemPadding: 20px;
		&{
			width: 200px; height: 130px; border-radius: 20px;; background-color: #fff; cursor: pointer; padding: @itemPadding; text-align: left; transition: all .2s linear; position: relative; margin: 1.5% 1%;
		}
		&:hover {
			box-shadow: 0 15px 30px rgba(0,0,0,0.1); transform: translate3d(0, -2px, 0);
		}
		.title { font-weight: bold; font-size: 16px; position: relative; top: @itemPadding + 30; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; color: #2c3e50; }
		.no-sub { top: @itemPadding + 50; }
		.sub-title { font-size: 12px; color: #95949a; margin-top: 10px; position: relative; top: @itemPadding + 30; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }
		.icon { position: absolute; right: @itemPadding; top: @itemPadding; width: 20px; height: 20px; background-size: contain; border-radius: 50%; }
	}
	.empty-item { width: 200px; height: 0; margin: 0 1%; }
	@media screen and (max-width: 1400px) {
		.item-con { justify-content: space-around; }
	}
</style>



